:root {
  --primaryColor: #f15025;
  --mainWhite: #fff;
  --mainBlack: #000;
  --mainBG: #0e469a;
  --mainRed: red;
  --mainGreen: #318d34;
  --mainHeadFont: 'Orbitron', sans-serif;
  --sub-mainHeadFont: 'Oxanium', cursive;
  --mainBodyFont: 'Poppins', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  background-color: var(--mainBG);
  font-family: var(--mainBodyFont);
}

h1 {
  text-align: center;
  text-transform: capitalize;
  margin: 2rem 0;
  color: var(--mainWhite);
  font-family: var(--mainHeadFont);
  font-size: 1.25rem;
}

.expenses-header {
  font-size: 2.5rem;
  text-shadow: 1px 1px 0px var(--primaryColor), 1.5px 1.5px 0px var(--mainGreen);
}

.App {
  margin: 0 auto;
  width: 80%;
  background-color: var(--mainWhite);
  padding-bottom: 2rem;
  text-align: center;
  min-height: 300px;
}

.alert {
  width: 70%;
  margin: 0 auto;
  margin-top: 20px;
  min-height: 20px;
  border: 1px solid var(--mainWhite);
  color: var(--mainWhite);
  padding: 5px 20px;
  text-align: center;
}

.item-warning {
  font-size: 0.75rem;
  position: fixed;
  width: 40%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
  right: 5%;
  background-color: rgba(0, 0, 0, 0.75);
  color: var(--mainWhite);
  padding-top: 15px;
}

.item-warning button {
  font-size: 0.6rem;
}

.alert-danger {
  background-color: red;
}

.alert-success {
  background-color: var(--mainGreen);
}

.alert-item {
  color: red;
  font-style: italic;
}

.form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.form-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 1rem;
}

.form-group {
  padding: 1rem 0.75rem;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50%;
          flex: 0 0 50%;
}

label {
  display: block;
  color: var(--mainGrey);
  font-size: 1rem;
  text-transform: capitalize;
  font-weight: 500;
  font-family: var(--mainHeadFont);
}

.form-control {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--mainBG);
  border-radius: 0;
  outline: none;
  min-height: 2rem;
  width: 100%;
  font-size: 1rem;
  margin: 0 0 0.75rem 0;
  padding: 0 10px;
}

.button {
  font-family: var(--mainHeadFont);
  color: var(--mainWhite);
  background-color: var(--primaryColor);
  text-align: center;
  font-size: 1rem;
  outline: none;
  border: 2px solid var(--mainBG);
  font-weight: 600;
  padding: 10px 20px;
  cursor: pointer;
}

.warning-yes,
.warning-no {
  font-size: 0.85rem;
  border: 1px solid var(--mainBG);
  font-weight: 500;
  padding: 5px 10px;
  margin: 10px;
}

.warning-yes {
  background-color: var(--mainGreen);
}

.warning {
  height: 100px;
  width: 80%;
  margin: 0 auto;
  color: red;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-top: 20px;
}

.add-button {
  background-color: #318d34;
  margin-bottom: 10px;
}

.clear-button {
  margin-top: 15px;
}

.item-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.item {
  list-style: none;
  width: 70%;
  border: 1px solid gray;
  text-align: left;
  margin: 10px;
  padding: 5px 0px 5px 5px;
  font-weight: 500;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-transform: capitalize;
}

.item-name {
  color: #60ff78;
  font-weight: 500;
}

.info {
  width: calc(100% - 60px);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.modify-buttons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  width: 65px;
}

.edit-button,
.delete-button {
  cursor: pointer;
  outline: none;
  border: 1px solid gray;
  padding: 2px 2px 0px 2px;
  border-radius: 3px;
}

.edit-button {
  color: var(--mainBG);
  background-color: #fff;
}

.delete-button {
  color: var(--mainRed);
  background-color: #fff;
}

.amount {
  font-weight: 500;
}
